import BScroll from "better-scroll";
export default {
  data() {
    return {
      isFirst: true, // 首次计算高度
      scrollY: 0, // 定义的Y滚动轴及初始值
      categoryHeight: "", // 控件高度
      rightLiTops: [], //所有分类头部位置
      categoryTypeList: [] // 菜单集合
    };
  },
  watch: {
    categoryTypeList() {
      //监听数据
      this.$nextTick(() => {
        this.initScroll();
        this.initRightHeight();
      });
    }
  },
  computed: {
    //动态绑定class类名
    currentIndex(index) {
      const { scrollY, rightLiTops } = this;
      return rightLiTops.findIndex((tops, index) => {
        this.initleftScroll(index); //调用左右联调滚动效果
        return scrollY >= tops && scrollY < rightLiTops[index + 1];
      });
      return 0;
    }
  },
  beforeDestroy() {
    this.leftScroll.destroy();
    this.rightScroll.destroy();
  },
  mounted() {},
  methods: {
    init(title) {
      // 首次计算高度
      if (this.isFirst) {
        // 动态控制分类视图的高度
        this.categoryHeight = "height:" + (window.innerHeight - 55 - 53) + "px";
        // 读取列表数据
        this.categoryTypeList = this.$parent.categoryList.slice(
          1,
          this.$parent.categoryList.length
        );
        this.isFirst = false;
      } else {
        for (let index = 0; index < this.categoryTypeList.length; index++) {
          if (title == this.categoryTypeList[index].CategoryName) {
            this.leftItemClick(index);
            break;
          }
        }
      }
    },
    initScroll() {
      this.leftScroll = new BScroll(this.$refs.leftScroll, {
        click: true,
        probeType: 3
      });
      this.rightScroll = new BScroll(this.$refs.rightScroll, {
        click: true,
        bounce: false,
        probeType: 3
      });
      this.leftScroll.on("scroll", pos => {});
      this.rightScroll.on("scroll", pos => {
        this.scrollY = Math.abs(pos.y);
      });
    },
    // 左右联调
    initleftScroll(index) {
      let menu = this.$refs.leftScroll;
      let el = menu[index];
      this.leftScroll.scrollToElement(el, 300, 0, -300);
    },
    // 求出右边列表的高度
    initRightHeight() {
      let itemArray = []; //定义一个伪数组
      let top = 0;
      itemArray.push(top);
      // 获取右边所有li的
      let allList = this.$refs.rightScroll.getElementsByClassName("rightItem");
      //allList伪数组转化成真数组
      Array.prototype.slice.call(allList).forEach(item => {
        top += item.clientHeight; //获取所有li的每一个高度
        itemArray.push(top);
      });
      this.rightLiTops = itemArray;
    },
    // 左边item点击事件
    leftItemClick(index) {
      this.scrollY = this.rightLiTops[index];
      this.rightScroll.scrollTo(0, -this.scrollY);
    },
    // 专区分类点击事件
    prefectureItemClick(item) {
      // 跳转商品列表
      this.$router.push({
        name: "commodityList",
        params: {
          item: item
        }
      });
    }
  }
};



